html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #1A1B15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  text-align: center;
  color: #0DA6F3;
  margin: 0 0 2.5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../img/1.avif);
  height: 20rem;
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  padding-top: 3rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
  .contenedor-app .app::-webkit-scrollbar {
    display: none;
  }
}

.nombre-pagina {
  font-weight: 900;
  margin: 2rem 0 3rem 0;
}
@media (min-width: 768px) {
  .nombre-pagina {
    margin: 5rem 0;
  }
}

.descripcion-pagina {
  text-align: center;
}

.barra-opciones {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-opciones {
    display: flex;
    justify-content: space-between;
  }
}
.barra-opciones .boton {
  margin: 0 0 1rem 0;
}
@media (min-width: 768px) {
  .barra-opciones .boton {
    margin-top: 2rem;
  }
}

.seccion {
  padding-top: 2.5rem;
  display: none;
}
.seccion .descripcion-paso {
  text-align: center;
}
.seccion.mostrar {
  display: block;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 2.5rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 0.2s;
}
.listado-servicios .servicio:hover {
  transform: scale(1.025);
  cursor: pointer;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0DA6F3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio p {
  text-align: center;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio p:last-of-type {
  margin: 0;
}
.listado-servicios .servicio .nombre-servicio {
  color: #1A1B15;
}
.listado-servicios .servicio .precio-servicio {
  color: #0DA6F3;
  font-size: 3rem;
  font-weight: 700;
}

.contenido-resumen h3 {
  margin: 3rem 0 2rem 0;
}
.contenido-resumen h3:last-of-type {
  margin-bottom: 0;
}
.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen p span {
  color: #0DA6F3;
  font-size: 2.4rem;
  font-weight: 700;
}

.contenedor-servicio {
  padding: 2rem 0;
  border-bottom: 1px solid #E1E1E1;
}

@media (min-width: 768px) {
  .izquierda-derecha {
    display: flex;
    justify-content: space-between;
  }
}
.izquierda-derecha .boton {
  background-color: #076493;
  margin-top: 3rem;
}
.izquierda-derecha .boton:hover {
  background-color: #FFFFFF;
  color: #0DA6F3;
}
@media (min-width: 768px) {
  .izquierda-derecha .boton {
    margin-bottom: 0;
  }
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  color: #0DA6F3;
  font-weight: 700;
}
.citas li span, .servicios li span, .citas li .servicio, .servicios li .servicio {
  color: #FFFFFF;
  font-weight: 400;
}
.citas li h3, .servicios li h3 {
  padding-top: 1rem;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .boton-eliminar {
  border-radius: 1rem;
  padding: 1rem 3rem;
  margin: 0 0 1rem 0;
}

.servicios .boton,
[value=Crear].boton,
[value="Guardar Cambios"].boton {
  background-color: #076493;
}
.servicios .boton:hover,
[value=Crear].boton:hover,
[value="Guardar Cambios"].boton:hover {
  background-color: #FFFFFF;
  color: #0DA6F3;
}

.derecha {
  display: flex;
  justify-content: flex-end;
}

.formulario {
  width: 100%;
}
@media (min-width: 768px) {
  .formulario {
    margin-top: 3rem;
  }
}

.campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.campo label {
  flex: 0 0 10rem;
  color: #FFFFFF;
}
.campo input {
  flex: 1;
  border-radius: 1rem;
  border: none;
  padding: 1.25rem;
}
.campo input:disabled {
  background: #898e6e;
  color: #FFFFFF;
  cursor: not-allowed;
}

.boton {
  background-color: #0DA6F3;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  padding: 1.5rem 4rem;
  margin: 5rem 0;
  display: block;
  width: 100%;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}

.boton-eliminar {
  background-color: #CB0000;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border: none;
  padding: 1.5rem 4rem;
  margin: 5rem 0;
  display: block;
  width: 100%;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  background-color: #008080;
  color: #FFFFFF;
  text-align: center;
  display: block;
  border-radius: 1rem;
  padding: 1rem 0;
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .acciones a {
    padding: 1rem 3rem;
  }
}

.aviso {
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  border-radius: 1rem;
  padding: 1.25rem;
  margin-bottom: 2rem;
  width: 100%;
  display: block;
}
.aviso.error {
  background-color: #ffcbcb;
  color: #CB0000;
  margin-top: -1.5rem;
}
.aviso.correcto {
  background-color: #329F00;
  color: #FFFFFF;
}

.incorrecto {
  background-color: #a0a48b;
  font-size: 1.5rem;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  border-radius: 1rem;
  padding: 1rem;
  margin: -1rem 0 1.5rem;
  width: 100%;
  display: block;
}

@media (min-width: 768px) {
  .tabs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.tabs button {
  background-color: #0DA6F3;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  border: none;
  padding: 1rem;
  margin-bottom: 1.5rem;
  display: block;
  width: 100%;
  transition: background-color 300ms ease;
}
.tabs button:hover:not(.actual) {
  background-color: #0a85c3;
  cursor: pointer;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0DA6F3;
}

@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
    margin: 5rem 0;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}
.paginacion button.ocultar {
  visibility: hidden;
}

.barra {
  margin: 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  font-weight: 700;
  text-align: center;
}
.barra a {
  background-color: #008080;
  color: #FFFFFF;
  text-align: center;
  display: block;
  border-radius: 1rem;
  padding: 1rem 0;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .barra a {
    padding: 1rem 3rem;
    margin: 0;
  }
}