@use 'base/variables' as v;

.citas {
	list-style: none;
	padding: 0;

	li {
		padding: 1rem;
		border-top: 1px solid v.$blanco;

		p {
			color: v.$azul;
			font-weight: v.$bold;
		}

		span, .servicio {
			color: v.$blanco;
			font-weight: v.$regular;
		}

		h3 {
			padding-top: 1rem;
		}
	}
}

.servicios {
	@extend .citas;

	.acciones {
		margin: 0;
	}

	.boton,
	.boton-eliminar {
		border-radius: 1rem;
		padding: 1rem 3rem;
		margin: 0 0 1rem 0;
	}
}

.servicios .boton,
[value="Crear"].boton,
[value="Guardar Cambios"].boton {
	background-color: darken(v.$azul, 20%);

    &:hover {
        background-color: v.$blanco;
        color: v.$azul;
    }
}

.derecha {
	display: flex;
	justify-content: flex-end;
}

