@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios {
	display: grid;
	gap: 2rem;

	// :hover:not(p) {
	// 	transform: scale(1.025);
	// }

	@include m.tablet {
		@include m.grid(2, 2rem);
	}

	.servicio {
		background-color: v.$blanco;
		padding: calc(v.$separacion / 2);
		border-radius: 1rem;

		transition-property: transform;
		transition-duration: .2s;

		&:hover {
			transform: scale(1.025);
			cursor: pointer;
		}

		&.seleccionado {
			background-color: v.$azul;

			p {
				color: v.$blanco;
			}
		}

		p {
			text-align: center;
			margin: 0 0 calc(v.$separacion / 4) 0;

			&:last-of-type {
				margin: 0;
			}
		}

		.nombre-servicio {
			color: v.$negro;
		}

		.precio-servicio {
			color: v.$azul;
			font-size: 3rem;
			font-weight: v.$bold;
		}
	}
}

