@use 'variables' as v;

// Media Queries
@mixin telefono {
	@media (min-width: v.$telefono) {
		@content;
	}
}

@mixin tablet {
	@media (min-width: v.$tablet) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: v.$desktop) {
		@content;
	}
}

// Grid
@mixin grid($columnas, $espaciado) {
	display: grid;
	grid-template-columns: repeat($columnas, 1fr);
	gap: $espaciado;
}

// Botón
@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    color: $colorTexto;
    font-size: 2rem;
    font-weight: v.$bold;
    text-align: center;
    border: none;
    padding: 1.5rem 4rem;
    margin: 5rem 0;
    display: block;
    width: 100%;

    transition-property: background-color;
    transition-duration: .3s;

    &:hover {
        background-color: darken($bgColor, 10%);
        cursor: pointer;
    }

    @include tablet {
        width: auto;
    }
}

