@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
	margin-top: 5rem;

	@include m.tablet {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	a {
		background-color: v.$verde-opaco;
		color: v.$blanco;
		text-align: center;
		display: block;
		border-radius: 1rem;
		padding: 1rem 0;
		margin-bottom: calc(v.$separacion / 2);
		
		@include m.tablet {
			padding: 1rem 3rem;
		}
	}
}

