@use 'variables' as v;


h1, h2, h3 {
    font-family: v.$fuentePrincipal;
    font-weight: v.$bold;
    text-align: center;
    color: v.$azul;
    margin: 0 0 calc(v.$separacion / 2) 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    font-size: 2rem;
    color: v.$blanco;
}

