@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
	width: 100%;

	@include m.tablet {
		margin-top: 3rem;
	}
}

.campo {
	display: flex;
	align-items: center;
	margin-bottom: 2rem;

	label {
		flex: 0 0 10rem;
		color: v.$blanco;
	}

	input {
		// all: unset;
		flex: 1;
		border-radius: 1rem;
		border: none;
		padding: calc(v.$separacion / 4);

		&:disabled {
			background: lighten(v.$negro, 40%);
			color: v.$blanco;
			cursor: not-allowed;
		}
	}
}

