@use 'base/variables' as v;

.aviso {
	font-weight: v.$bold;
	text-transform: uppercase;
	text-align: center;
	border-radius: 1rem;
	padding: calc(v.$separacion / 4);
	margin-bottom: 2rem;
	width: 100%;
	display: block;

	&.error {
		background-color: lighten(v.$rojo, 50%);
		color: v.$rojo;
		margin-top: -1.5rem;
	}

	&.correcto {
		background-color: v.$verde;
		color: v.$blanco;		
	}
}

.incorrecto {
	background-color: lighten(v.$negro, 50%);
	font-size: 1.5rem;
	font-weight: v.$regular;
	text-transform: uppercase;
	text-align: center;
	border-radius: 1rem;
	padding: 1rem;
	margin: -1rem 0 1.5rem;
	width: 100%;
	display: block;
}

