@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
	@include m.tablet {
		@include m.grid(2, 0);
		min-height: 100vh;
	}

	.imagen {
		background-image: url(../img/1.avif);
		height: 20rem;
		background-size: cover;
		background-position: center center;

		@include m.tablet {
			height: auto;
		}
	}

	.app {
		width: 95%;
		padding-top: 3rem;
		margin: 0 auto;

		@include m.tablet {
			overflow: scroll;
			// overflow-x: hidden;
			// overflow-y: hidden;

			&::-webkit-scrollbar {
			 	display: none;
			}
		}
	}
}

.nombre-pagina {
	font-weight: v.$black;
	margin: 2rem 0 3rem 0;

	@include m.tablet {
		margin: 5rem 0;
	}	
}

.descripcion-pagina {
	text-align: center;
}

.barra-opciones {
	margin-bottom: v.$separacion;

	@include m.tablet {
		display: flex;
		justify-content: space-between;
	}

	.boton {
		margin: 0 0 1rem 0;

		@include m.tablet {
			margin-top: 2rem;
		}
	}
}

