@use 'variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    background-color: v.$negro;
    color: v.$blanco;
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    display: flex;
    flex-direction: column;
    min-height: 100%;

    @include m.tablet {
        height: 100%;        
    }
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
.contenedor {
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

