@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-resumen {
	h3 {
		margin: 3rem 0 2rem 0;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	p {
		margin-bottom: 1rem;

		span {
			color: v.$azul;
			font-size: 2.4rem;
			font-weight: v.$bold;
		}
	}
}	

.contenedor-servicio {
	padding: 2rem 0;
	border-bottom: 1px solid v.$gris;
}

.izquierda-derecha {
	@include m.tablet {
		display: flex;
		justify-content: space-between;
	}

	.boton {
		background-color: darken(v.$azul, 20%);
		margin-top: 3rem;

	    &:hover {
	        background-color: v.$blanco;
	        color: v.$azul;
	    }

		@include m.tablet {
			margin-bottom: 0;
		}
	}
}

