@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
	margin: 0;

	@include m.tablet {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	p {
		font-weight: v.$bold;
		text-align: center;
	}

	a {
		background-color: v.$verde-opaco;
		color: v.$blanco;
		text-align: center;
		display: block;
		border-radius: 1rem;
		padding: 1rem 0;
		margin-bottom: 4rem;
		
		@include m.tablet {
			padding: 1rem 3rem;
			margin: 0;
		}
	}
}

